* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #000;
}

html {
  --links-color: #000;
  --bg-color: #fff;
  --hover-bg: #00acdc;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
}

.header {
  background-color: var(--bg-color);
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  display: flex;
}

.nav {
  position: relative;
  right: 50px;
}

.nav__list {
  justify-content: right;
  align-items: center;
  display: flex;
}

.nav__item {
  margin: 0 10px;
  list-style: none;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  text-transform: uppercase;
  color: var(--links-color);
  justify-content: center;
  align-items: center;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: flex;
}

.is-active {
  color: var(--hover-bg);
  position: relative;
}

.img__container {
  justify-content: left;
  align-items: center;
  width: 40px;
  height: 40px;
  display: flex;
  position: relative;
  left: 50px;
}

.img__link {
  height: 40px;
}

.nav__link:hover {
  color: var(--hover-bg);
}

.is-active:after {
  content: " ";
  background-color: var(--hover-bg);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  text-decoration: underline;
  position: absolute;
  bottom: 0;
  left: 0;
}
